import './style.css'
import * as THREE from 'three'
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader'
import {RGBELoader} from 'three/examples/jsm/loaders/RGBELoader';
import { EffectComposer } from 'three/examples/jsm/postprocessing/EffectComposer.js';
import { RenderPass } from 'three/examples/jsm/postprocessing/RenderPass.js';
import { UnrealBloomPass } from 'three/examples/jsm/postprocessing/UnrealBloomPass.js';

import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js'
import { PointerLockControls } from 'three/examples/jsm/controls/PointerLockControls.js';
import * as dat from 'lil-gui'

const loader = new THREE.TextureLoader();
const diffuse = loader.load("/textures/diffuse_colors.png");
const height = loader.load("/textures/height_lava.png");
const normal = loader.load("/textures/normal_lava.png");
const roughness = loader.load("/textures/roughness_lava.png");
const ao_map = loader.load("/textures/ao_lava.png");
const em_map = loader.load("/textures/emissiveMap_lava.png");

let composer;

const params = {
  exposure: 1,
  bloomStrength: 1,
  bloomThreshold: 0,
  bloomRadius: 0
};

const loader_gltf = new GLTFLoader();

const hdrEquirect = new RGBELoader().load(
    "/satara_night_no_lamps_2k.hdr",
    () => {
        hdrEquirect.mapping = THREE.EquirectangularReflectionMapping;
    }
);

const floor_mat = new THREE.MeshStandardMaterial({
    color: new THREE.Color('#1D1B1D'),
});

const bench = loader_gltf.load(
    '/mesh.gltf',
    function ( gltf ) {
        gltf.scene.scale.set(1, 1, 1);
        gltf.scene.position.y = 0.9;
        gltf.scene.traverse( ( child ) => {
            if(child instanceof THREE.Mesh){
                child.material = floor_mat;
            }
        });
        scene.add( gltf.scene );
    },

);
const canvas = document.querySelector('canvas.webgl');
let scene = new THREE.Scene();
let camera = new THREE.PerspectiveCamera(60, innerWidth / innerHeight, 1, 1000);
camera.position.set(5, 8, 13).setLength(10);
let renderer = new THREE.WebGLRenderer({
    antialias: true,
    alpha: true
});

renderer.setSize(innerWidth, innerHeight);
renderer.setClearColor(0xffffff, 0);
renderer.setPixelRatio( window.devicePixelRatio );
document.body.appendChild(renderer.domElement);
renderer.toneMapping = THREE.ACESFilmicToneMapping

const renderScene = new RenderPass( scene, camera );

const bloomPass = new UnrealBloomPass( new THREE.Vector2( window.innerWidth, window.innerHeight ), 1.5, 0.4, 0.85 );
bloomPass.threshold = params.bloomThreshold;
bloomPass.strength = params.bloomStrength;
bloomPass.radius = params.bloomRadius;

composer = new EffectComposer( renderer );
composer.addPass( renderScene );
composer.addPass( bloomPass );

camera.position.y = 50;
camera.position.z = 45;
camera.rotation.x = - 0.8;

// const ambientLight = new THREE.AmbientLight('#FFFFFF', 0.5);
// scene.add(ambientLight);

const spotlight = new THREE.SpotLight('#FFFFFF', 0.1);
spotlight.position.set( -50, 20, -10 );
spotlight.rotation.y = - 0.5;
scene.add(spotlight);

// const spotLightHelper = new THREE.SpotLightHelper( spotlight );
// scene.add( spotLightHelper );

const spotlight2 = new THREE.SpotLight('#FFFFFF', 0.1);
spotlight2.position.set( 60, 20, -60 );
spotlight2.rotation.y = - 0.5;
scene.add(spotlight2);

const pointLight = new THREE.PointLight('#FFFFFF', 0.4);
scene.add(pointLight);
pointLight.position.z = 60;
pointLight.position.y = 0;

const base = new THREE.BoxGeometry( 50, 50 );

let g = new THREE.PlaneGeometry(50, 50, 50, 50);
let g_2 = new THREE.PlaneGeometry(50, 50, 50, 50);
let g_3 = new THREE.PlaneGeometry(50, 50, 50, 50);

g.rotateX(-Math.PI * 0.5);
g_2.rotateX(-Math.PI * 0.5);
g_3.rotateX(-Math.PI * 0.5);

let uniforms = {
    center: {
        value: new THREE.Vector3()
      },
	rectSize: {value: new THREE.Vector2(11, 11)},
  rectWidth: {value: 0.2},
  color_square : {value: new THREE.Color('red')},
  color_alpha : {value: 0.001},
  color_entour : {value: new THREE.Color('red')},
  rectSize_entour: {value: new THREE.Vector2(12, 12)},
  rectWidth_entour: {value: 0.2},
  color_alpha_entour : {value: 0.001},
}

let m = new THREE.MeshStandardMaterial({
    map: diffuse,
    displacementMap: height,
    normalMap: normal,
    roughnessMap: roughness,
    aoMap: ao_map,
    displacementScale: 5,
    // emissive:new THREE.Color('#FF4500'),
    // emissiveMap: em_map,
    // emissiveIntensity: 1,
    envMap: hdrEquirect,
    envMapIntensity : 1,
onBeforeCompile: shader => {
  	shader.uniforms.rectSize = uniforms.rectSize;
    shader.uniforms.rectWidth = uniforms.rectWidth;
    shader.uniforms.rectSize_entour = uniforms.rectSize_entour;
    shader.uniforms.rectWidth_entour = uniforms.rectWidth_entour;
    shader.uniforms.center = uniforms.center;
    shader.uniforms.color_square = uniforms.color_square;
    shader.uniforms.color_alpha = uniforms.color_alpha;
    shader.uniforms.color_entour = uniforms.color_entour;
    shader.uniforms.color_alpha_entour = uniforms.color_alpha_entour;
    shader.vertexShader = `
    varying vec2 vPos;
    ${shader.vertexShader}
  `.replace(
      `#include <fog_vertex>`,
    `#include <fog_vertex>
        vPos = position.xz;
    `
  );
    shader.fragmentShader = `
        #define ss(a, b, c) smoothstep(a, b, c)
        uniform vec3 center;
        uniform vec3 color_square;
        uniform vec3 color_entour;
        uniform vec2 rectSize;
        uniform float rectWidth;
        uniform float color_alpha;
        varying vec2 vPos;
        uniform vec2 u_resolution;
        uniform vec2 rectSize_entour;
        uniform float rectWidth_entour;
        uniform float color_alpha_entour;
        float rectshape(vec2 position, vec2 scale){
          scale = vec2(0.5) - scale * 0.5;
          vec2 shaper = vec2(step(scale.x, position.x), step(scale.y, position.y));
          shaper *= vec2(step(scale.x, 1.0 - position.x), step(scale.y, 1.0 - position.y));
          return shaper.x * shaper.y;
        }
        ${shader.fragmentShader}
      `.replace(
        `#include <dithering_fragment>`,
        `#include <dithering_fragment>
        vec2 hSize = rectSize * 0.5;
        vec2 position = abs( vPos - center.xz ) - 0.3;
  
        vec3 color = color_square;
      
        float rectangle = rectshape(position, vec2(11, 11));
      
        color = vec3(rectangle);

        gl_FragColor.rgb = mix(gl_FragColor.rgb, color_square, rectangle * color_alpha);
        
        vec2 hSize2 = rectSize * 0.5;
        vec2 rPos = abs( vPos - center.xz ) - hSize2;
        
        float rect = float(abs(max(rPos.x,rPos.y)) < rectWidth * 0.5);
        
        gl_FragColor.rgb = mix(gl_FragColor.rgb, vec3(1.,1.,0.), rect * 0.5);

        vec2 hSize3 = rectSize_entour * 0.5;
        vec2 rPos3 = abs( vPos - center.xz ) - hSize3;
        
        float rect3 = float(abs(max(rPos3.x,rPos3.y)) < rectWidth_entour * 0.5);
        
        gl_FragColor.rgb = mix(gl_FragColor.rgb, color_entour, rect3 * color_alpha_entour);
        `
      );
  }
});
m.defines = {
	"USE_UV" : " " // just in case
}

let plane = new THREE.Mesh(g, m);
scene.add(plane);
plane.position.y = 0;

