let mouse = new THREE.Vector3();
let raycaster = new THREE.Raycaster();
let intersects = [];
let intersects2 = [];
var point = new THREE.Vector3();
window.addEventListener("resize", onWindowResize);

let clock = new THREE.Clock();

animate();

window.addEventListener("mousemove", function(event) {

    if (event.which == 1) {
        mouse.x = (event.clientX / window.innerWidth) * 2 - 1;
        mouse.y = -(event.clientY / window.innerHeight) * 2 + 1;
        raycaster.setFromCamera(mouse, camera);
        intersects = raycaster.intersectObject(plane, true);
        if (intersects.length != 0){
        plane.worldToLocal(point.copy(intersects[0].point));
        let point_2 = new THREE.Vector3(Math.round(point.x), 0, Math.round(point.z));
        uniforms.center.value.copy(point_2);
        uniforms.color_square.value = new THREE.Color('black');
        uniforms.color_alpha.value = 0.001;
        uniforms.color_alpha_entour.value = 0.5;
      }
        
    }else{
        uniforms.color_square.value = new THREE.Color('#edaf38');
        uniforms.color_alpha.value = 0.1;
        uniforms.color_alpha_entour.value = 0.001;
    }
   
  }, false);

  window.addEventListener("mouseup", function(event) {
    if (uniforms.center.value.x == -21) {
      window.location.href = "http://www.w3schools.com";
    }
  }, false);

function animate() {

  requestAnimationFrame( animate );
  let t = clock.getElapsedTime();

  const delta = clock.getDelta();

  composer.render();

}

function onWindowResize() {

  camera.aspect = innerWidth / innerHeight;
  camera.updateProjectionMatrix();

  renderer.setSize(innerWidth, innerHeight);

  renderer.setSize( window.innerWidth, window.innerHeight );
	composer.setSize( window.innerWidth, window.innerHeight );

}